import { cookies } from "next/headers"
import { type Language, getTranslation, type TranslationKey } from "./translations"

export async function getTranslations() {
  const cookieStore = cookies()
  const langCookie = cookieStore.get("kiloshare-language")
  const lang = (langCookie?.value === "en" || langCookie?.value === "fr" ? langCookie.value : "fr") as Language

  const t = (key: TranslationKey): string => {
    return getTranslation(key, lang)
  }

  return { t, lang }
}
